'use client';

export default function RenderPage() {
  return (
    <>
      <h1>JSX 渲染(render)範例</h1>
      <hr />
      <h2>！Number 的格式</h2>
      {1 - 1}
      <br />
      {NaN}
      <hr />
      <h2>！String 的格式</h2>
      這是沒被符號包起來的字串
      <br />
      {'這是放在花括號，又放在單引號裡的字串'}
      <br />
      {`這是可以和變數組合的字串${1 - 1}`}
      <hr />
      <h2>！Boolean 的格式 (不渲染)</h2>
      {true}
      <br />
      {false}
      <hr />
      <h2>！沒有的格式 (不渲染)</h2>
      {null}
      <br />
      {undefined}
      <hr />
      <h2>！Array 陣列</h2>
      {[1, 2, 3]}
      <br />
      {['a', 'b']}
      {/* {[<p>x</p>, <p>{1 - 1}</p>]} */}
      {[<p key="1">x</p>, <p key="2">{1 - 1}</p>]}
      <h2>！Object 物件</h2>
      {/* 以下物件寫法會大報錯 */}
      {/* {{a:1, b:2}} */}
      {JSON.stringify({a:1, b:2})}
    </>
  );
}
